import React, {SyntheticEvent} from "react";
import "./EditProfile.css"
import {Form} from "react-bootstrap";
import Button from '@material-ui/core/Button';
import store from "../../../../state/store";
import {editProfile, getPreferences, getUserPreferences, setUserPreferences} from "../../../../state/actions/editProfile"
import {logUserAction} from "../../../../state/actions/analytics";
import {ButtonClicks} from "../../../../state/models/analyticsModels";
import {Checkbox} from "@material-ui/core";
import validateProfileInput from "../../../../state/validations/validateProfileInput";

export interface EditProfileProps {
    pathname?: string;
}

export interface EditProfileState {
    username: string;
    newUsername: string;
    newEmail: string;
    password: string;
    newPassword: string;
    confirmPassword: string;
    errors: any;
    success: boolean;
    settings: any[];
    userSettings: any[];
}

class EditProfile extends React.Component<EditProfileProps, EditProfileState> {
    constructor(props: EditProfileProps) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            newUsername: '',
            newEmail: '',
            password: '',
            newPassword: '',
            confirmPassword: '',
            errors: {},
            success: false,
            settings: [],
            userSettings: []
        }
        this.getSetting();
    }

    async getSetting() {
        const settingsResponse = await getPreferences();
        if(settingsResponse.isSuccessful) {
            this.setState({settings: settingsResponse.data})
        }

        const userSettings = await getUserPreferences();
        if(userSettings.isSuccessful) {
            const settings = this.state.settings.map( (set: any) => {
                return {
                    id: set[0],
                    name: set[1],
                    value: userSettings.data.filter((pref: any) => {return (pref.preference_id === set[0])})[0] ?
                        !!(userSettings.data.filter((pref: any) => {return (pref.preference_id === set[0])})[0].val) :
                        false
                }
            });
            this.setState({userSettings: settings})
        }
    }

    handleChangeNewUsername = (event: SyntheticEvent) => {
        const newUsername = (event.target as HTMLInputElement).value;
        this.setState({newUsername});
    }

    handleChangeNewEmail = (event: SyntheticEvent) => {
        const newEmail = (event.target as HTMLInputElement).value;
        this.setState({newEmail});
    }

    handleChangeCurrentPassword = (event: SyntheticEvent) => {
        const password = (event.target as HTMLInputElement).value;
        this.setState({password});
    }

    handleChangeNewPassword = (event: SyntheticEvent) => {
        const newPassword = (event.target as HTMLInputElement).value;
        this.setState({newPassword});
    }

    handleChangeConfirmNewPassword = (event: SyntheticEvent) => {
        const confirmPassword = (event.target as HTMLInputElement).value;
        this.setState({confirmPassword});
    }

    handleClickSaveProfile = async (event: SyntheticEvent) => {
        event.preventDefault();
        logUserAction(ButtonClicks.EDIT)
        if(this.isUpdateProfileFormValid()){
            const data = {
                username: this.state.username,
                new_username: this.state.newUsername,
                email: this.state.newEmail,
                password: this.state.password,
                new_password: this.state.confirmPassword,
            }

            const {errors, isSuccessful} = await editProfile(data);
            //set errors regardless of success (may be empty -> erase previous ones)
            this.setState({errors});
            this.setState({success: isSuccessful});
            if(isSuccessful) {
                (document.getElementById("username-form") as HTMLFormElement)?.reset();
                (document.getElementById("password-form") as HTMLFormElement)?.reset();
            }

        }
    }

    isUpdateProfileFormValid(){
        const{errors, isValid} = validateProfileInput(this.state);
        //set errors regardless of success (may be empty -> erase previous ones)
        this.setState({errors});
        return isValid;
    }

    handleChange = (event: any) => {
        if(event.target.name.includes('webcam')){
            logUserAction(ButtonClicks.WEBCAM_SETTING);
            let fieldId: any = undefined;
            //update userSettings
            const settingsToUpdate = this.state.userSettings.map((set: any) => {
                if(set.name === event.target.name) {
                    fieldId = set.id;
                    return {
                        ...set,
                        value: event.target.checked
                    }
                } else return set;
            });
            this.setState({userSettings: settingsToUpdate})
            if(fieldId) {
                setUserPreferences(fieldId, event.target.checked);
            }

        } else {
            console.log('functionality not implemented');
        }
    };


    render() {
        return (
            <>
            <h3 className="header-dashboard">Edit Profile</h3>
            <div className="edit-profile-component">
                <div className="main-section">

                    <Form className="form" id='username-form'>

                        <Form.Group controlId="formBasicUsername" className="group-form">

                            <div className="form-row">
                                <div className="form-cell">
                                    <span>New Username</span>
                                </div>
                                <div className="form-cell">
                                    <Form.Control
                                        className="form-input"
                                        data-cy='edit-new-username-input'
                                        type="text"
                                        placeholder="New Username"
                                        onChange={this.handleChangeNewUsername}
                                    />
                                </div>
                            </div>

                            {this.state.errors.newUsername ? (
                                <p data-cy='edit-newUsername-error' className='invalid-input'>{this.state.errors.newUsername}</p>
                            ) : null}

                            <div className="form-row">
                                <div className="form-cell">
                                    <span>New Email</span>
                                </div>
                                <div className="form-cell">
                                    <Form.Control
                                        className="form-input"
                                        data-cy='edit-new-email-input'
                                        type="text"
                                        placeholder="New Email"
                                        onChange={this.handleChangeNewEmail}
                                    />
                                </div>
                            </div>

                            {this.state.errors.email ? (
                                <p data-cy='edit-email-error' className='invalid-input'>{this.state.errors.email}</p>
                            ) : null}

                        </Form.Group>

                    </Form>
                </div>

                <div className="main-section">
                    <div className="section-title">
                        <span>Change Password</span>
                    </div>

                    <Form className="form" id='password-form'>

                        <Form.Group controlId="formBasicPassword" className="group-form">

                            <div className="form-row">
                                <div className="form-cell">
                                    <span>Current Password</span>
                                </div>
                                <div className="form-cell">
                                    <Form.Control
                                        className="form-input"
                                        data-cy='edit-password-input'
                                        type="password"
                                        placeholder="Current Password"
                                        onChange={this.handleChangeCurrentPassword}
                                    />
                                </div>
                            </div>

                            {this.state.errors.password ? (
                                <p data-cy='edit-password-error' className='invalid-input'>{this.state.errors.password}</p>
                            ) : null}

                            <div className="form-row">
                                <div className="form-cell">
                                    <span>New Password</span>
                                </div>
                                <div className="form-cell">
                                    <Form.Control
                                        className="form-input"
                                        data-cy='edit-new-password-input'
                                        type="password"
                                        placeholder="New Password"
                                        onChange={this.handleChangeNewPassword}
                                    />
                                </div>
                            </div>

                            <div className="form-row">
                                <div className="form-cell">
                                    <span>Confirm Password</span>
                                </div>
                                <div className="form-cell">
                                    <Form.Control
                                        className="form-input"
                                        data-cy='confirm-new-password-input'
                                        type="password"
                                        placeholder="Confirm New Password"
                                        onChange={this.handleChangeConfirmNewPassword}
                                    />
                                </div>
                            </div>

                            {this.state.errors.newPassword ? (
                                <p data-cy='edit-newPassword-error' className='invalid-input'>{this.state.errors.newPassword}</p>
                            ) : null}

                            {this.state.success ? (
                                <p data-cy='edit-profile-success' className='success-message'>Profile successfully updated</p>
                            ) : null}

                        </Form.Group>
                    </Form>

                    <div className="save-button-container">
                        <Button variant="contained" data-cy='edit-profile-button' onClick={this.handleClickSaveProfile} >
                            <span className='white-txt'>Save</span>
                        </Button>
                    </div>
                </div>

                <div className="main-section">
                    <div className="section-title">
                        <span>Settings</span>
                    </div>

                    <Form className="settings-form">
                        <Form.Group controlId="formBasicSettings" className="group-form">
                            {this.state.settings && this.state.userSettings ?
                                this.state.userSettings.map( (setting) => {
                                    return <div className="form-cell settings-cell" key={setting.id}>
                                        <Checkbox
                                            onChange={this.handleChange}
                                            inputProps={{ 'aria-label': 'primary checkbox' }}
                                            name={setting.name}
                                            disabled={setting.name.includes('newsletter')}
                                            checked={setting.value}
                                        />
                                        <span>{setting.name}</span>
                                    </div>
                                })
                            : null}

                        </Form.Group>
                    </Form>

                </div>

                </div>
            </>
        )
    }
}
export default EditProfile;
