import React from "react";
import { connect } from "react-redux";
import "./Games.css";
import store from "../../../../state/store";
import { NavLink } from "react-router-dom";
import { Card, CardColumns } from "react-bootstrap";
import CardContainer from "../../../../common/components/cards/CardContainer";
import { Paths } from "../../../../App";
import {getGames} from "../../../../state/actions/games";


interface Game {
    game_id: number;
    name: string;
    description: string;
    game_type: boolean; // shouldn't be a boolean - TODO ask the backend why
    resources: string;
}


export interface GamesProps {
    pathname?: string;
}

export interface GamesState {
    username: string;
    games: Game[];

}

class Games extends React.Component<GamesProps, GamesState>{
    constructor(props: GamesProps) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            games: []
        }
    }

    async componentDidMount() {
        const games: Game[] = await getGames();
        console.log(games);
        this.setState({ games })
    }

    getHeader(){
        if(this.props.pathname?.includes('games')) {
            return (
                <>
                    <h3 className='header-dashboard'>Games</h3>
                    <h6 className='description'>Click on a game to start having fun</h6>
                </>
            )
        }
    }

    render() {
        return (
            <>
                {this.getHeader()}
                <div className="games-wrapper">
                    <CardColumns>
                        {this.state.games
                            ? this.state.games.map(
                                (game: Game) => {
                                    return <Card
                                        style={{ width: '70%', marginTop: '10%' }}
                                        key={game.game_id}>
                                        <NavLink key={game.game_id} to={Paths.games + '/'+ game.game_id} className="no-margin">
                                            <CardContainer
                                                name={game.name}
                                                description={game.description}
                                                imageUrl={game.resources}
                                                hideProgress
                                            />
                                        </NavLink>
                                    </Card>
                                }
                            )
                            : null}
                    </CardColumns>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state: any) => ({
    games: state.games,
})
const mapDispatchToProps = {
    getGames: getGames
}

export default connect(mapStateToProps, mapDispatchToProps)(Games);
