
export function getIdsFromPath(pathname: string) {
    let lessonId = undefined;
    let moduleId = undefined;
    let quizId = undefined;
    let progress = undefined;

    if (pathname.includes('quizzes') && !pathname.includes('modules')) {
        moduleId = pathname.substring(pathname.indexOf('quizzes/')+8);

    } else if (pathname.includes('quiz/')) {
        quizId = pathname.substring(pathname.indexOf('quiz/')+5);

    } else if(pathname.includes('lessons/')) {
        moduleId = pathname.substring(pathname.indexOf('lessons/')+8);
        if(moduleId.includes('/')) {
            const params = moduleId.split('/')//:moduleId/:lessonId/:progress"
            progress = params[2];
            lessonId = params[1];
            moduleId = params[0];
        }
    }
    return {moduleId, lessonId, quizId, progress};
}
