import {GET_GAMES} from "../models/types";
import axios from "axios";
import {BASE_URL} from "../../environment";
import {logError, logServerRequestTime} from "./analytics";
import {RequestName, STACK} from "../models/analyticsModels";
import store from "../store";


export async function getGames() {
    try {
        const sentTime = (new Date()).getTime();
        let response = await  axios.get(BASE_URL + '/games')
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.GAMES, receivedTime-sentTime);
        store.dispatch({
            type: GET_GAMES,
            payload: response.data?.games
        })
        return response.data?.games;
    } catch(e){
        logError(RequestName.GAMES, e, STACK.SERVER)
        console.error("Failed to get games", e);
        return null;
    }
}

