import React from "react";
import { connect } from "react-redux";
import { QuizQuestion } from "../types/Quiz";
import "./MatchAndSign.css";
import Webcam from "react-webcam";
import {Col, Container, Row} from "react-bootstrap";
import {sendCameraInput} from "../../../../../state/actions/quizzes";

export interface CameraQProps {
    question: QuizQuestion;
    module: string
    onQuestionAnswered: (e: React.MouseEvent) => void;
    onQuestionSkipped: (e: React.MouseEvent) => void;
}

export interface CameraQState {
    webcamRef: any;
    imageSrc: any;
    timerOn: boolean;
    timerCount: number;
    isCameraUnavailable: boolean;
    answer: string;
}

const TIMER: number = 3;

class MatchAndSign extends React.Component<CameraQProps, CameraQState>{
    constructor(props: CameraQProps) {
        super(props);
        this.state = {
            webcamRef: null,
            imageSrc: null,
            timerOn: false,
            timerCount: TIMER,
            isCameraUnavailable: false,
            answer: ''
        }
        this.capture = this.capture.bind(this);
    }

    componentWillMount() {
        setTimeout(() => {
            const element: any = document.getElementsByClassName('camera-quiz-wrapper')[0];
            element.style["visibility"] = 'visible';
        }, 1000);
    }


    setRef = (webcamRef: any) => {
        this.setState({webcamRef});
    }

    capture(e: React.MouseEvent) {
        this.setState({timerOn: true})

        const int = setInterval(() => {
            if (this.state.timerOn) {
                const timerCount = this.state.timerCount - 1;
                if(timerCount >= 0) {
                    this.setState({timerCount})
                } else {
                    this.setState({timerOn: false});
                }

            } else {
                try{
                    const imageSrc = this.state.webcamRef.getScreenshot();
                    this.setState({imageSrc});
                    sendCameraInput(imageSrc, this.props.module).then((res) => {
                        this.setState({answer: res.symbol});
                        this.props.onQuestionAnswered(e);
                    });
                    clearInterval(int);

                } catch(err) {
                    console.error(err);
                }
            }
        }, 1000);
    }

    handleWebcamUnavailable = () => {
        this.setState({isCameraUnavailable: true});
    }

    skip(e: React.MouseEvent) {
        this.props.onQuestionSkipped(e);
    }

    isRightAnswer = () => {
        return this.props.question.answer.toLowerCase() === this.state.answer.toLowerCase()
    }


    render() {
        const videoConstraints = {
            width: 640,
            height: 480,
            facingMode: "user"
        };

        return (
            <>
                <div className="camera-quiz-wrapper">
                    <Container>
                        <Row>
                            <Col>
                                <h4>{this.props.question.question}</h4>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                { this.state.isCameraUnavailable
                                    ?
                                        <div className="skip-question-wrapper">
                                            <span className="skip-text-wrapper">Camera unavailable. Please change your browsers permissions or skip the question</span>
                                            <div className="skip-button-wrapper">
                                                <button onClick={(event) => this.skip(event)}>Skip</button>
                                            </div>
                                        </div>
                                    :
                                        <>
                                            <Webcam
                                                videoConstraints={videoConstraints}
                                                mirrored
                                                audio={false}
                                                ref={this.setRef}
                                                onUserMediaError={this.handleWebcamUnavailable}
                                            />
                                            { this.state.webcamRef ?
                                                <div className="box-wrapper">
                                                    <div className="box"/>
                                                </div>
                                                : null}

                                            <div className="button-wrapper">
                                                <button disabled={this.state.answer !== ''} onClick={(event) => this.capture(event)}>Capture photo</button>
                                            </div>

                                            { this.state.timerOn ?
                                                <div className="counter-wrapper">
                                                    <h1 className="counter">{this.state.timerCount}</h1>
                                                </div>
                                                : null
                                            }
                                        </>
                                }
                                {this.state.answer !== ''
                                    ? (this.isRightAnswer()
                                        ? <div className="answer-feedback"> <span>{this.state.answer} is </span> <span className="correct-answer" >Correct!</span></div>
                                        : <div className="answer-feedback"> <span>{this.state.answer} is </span> <span className="wrong-answer">Wrong</span></div>)
                                    : null}
                            </Col>
                        </Row>
                    </Container>
                </div>

            </>
        )
    }
}
const mapDispatchToProps = {
}

export default connect(null, mapDispatchToProps)(MatchAndSign);
