import React from "react";
import { connect } from "react-redux";
import "./Freecam.css";
import Webcam from "react-webcam";
import {Col, Container, Row, ToggleButton, ToggleButtonGroup} from "react-bootstrap";
import {sendCameraInput} from "../../../../../state/actions/quizzes";

export interface CameraQProps {
}

export interface CameraQState {
    webcamRef: any;
    imageSrc: any;
    timerOn: boolean;
    timerCount: number;
    isCameraUnavailable: boolean;
    answer: string;
    currentModule:number;
}

const TIMER: number = 3;

class Freecam extends React.Component<CameraQProps, CameraQState>{
    constructor(props: CameraQProps) {
        super(props);
        this.state = {
            webcamRef: null,
            imageSrc: null,
            timerOn: false,
            timerCount: TIMER,
            isCameraUnavailable: false,
            answer: '',
            currentModule : 2
        }
        this.capture = this.capture.bind(this);
        this.toggleModule = this.toggleModule.bind(this);
    }

    componentWillMount() {
        setTimeout(() => {
            const element: any = document.getElementsByClassName('camera-game-wrapper')[0];
            element.style["visibility"] = 'visible';
        }, 1000);
    }


    setRef = (webcamRef: any) => {
        this.setState({webcamRef});
    }

    capture(e: React.MouseEvent) {
        this.setState({timerOn: true})

        const int = setInterval(() => {
            if (this.state.timerOn) {
                const timerCount = this.state.timerCount - 1;
                if(timerCount >= 0) {
                    this.setState({timerCount})
                    
                }else{
                    this.setState({timerOn: false});
                }

            } else {
                try{
                    const imageSrc = this.state.webcamRef.getScreenshot();
                    this.setState({imageSrc});
                    sendCameraInput(imageSrc,this.state.currentModule.toString()).then((res) => {
                        this.setState({answer: res.symbol});
                    });
                    clearInterval(int);

                } catch(err) {
                    console.error(err);
                }
            }
        }, 1000);
    }

    handleWebcamUnavailable = () => {
        this.setState({isCameraUnavailable: true});
    }
    getHeader(){
            return (
                <>
                    <h3 className='header-dashboard'>Freecam</h3>
                    <h6 className='description'>Try out any signs to see what letter your saying</h6>
                </>
            )
    }
    toggleModule(){
        if (this.state.currentModule == 1){
            this.setState({currentModule: 2})
        }
        else{
            this.setState({currentModule: 1})
        }
    }
    render() {
        const videoConstraints = {
            width: 640,
            height: 480,
            facingMode: "user"
        };

        return (
            <>
                {this.getHeader()}
                <div className="game-content-wrapper">
                    <div className="camera-game-wrapper">
                        <Container>
                            <Row>
                                <Col>
                                    <h4>Enter a sign to see what the reading is</h4>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    { this.state.isCameraUnavailable
                                        ?
                                            <div className="skip-question-wrapper">
                                                <span className="skip-text-wrapper">Camera unavailable. Please change your browsers permissions or skip the question</span>
                                            </div>
                                        :
                                            <>
                                                <Webcam
                                                    videoConstraints={videoConstraints}
                                                    mirrored
                                                    audio={false}
                                                    ref={this.setRef}
                                                    onUserMediaError={this.handleWebcamUnavailable}
                                                />
                                                { this.state.webcamRef ?
                                                    <div className="box-wrapper">
                                                        <div className="box"/>
                                                    </div>
                                                    : null}

                                                <div className="button-wrapper">
                                                    <button  onClick={(event) => this.capture(event)}>Capture photo</button>
                                                </div>

                                                { this.state.timerOn ?
                                                    <div className="counter-wrapper">
                                                        <h1 className="counter">{this.state.timerCount}</h1>
                                                    </div>
                                                    : null
                                                }
                                            </>
                                    }
                                    {this.state.answer !== ''
                                        ? <div className="answer-feedback"> The sign is a <span className="correct-answer">{this.state.answer} </span></div>
                                        : null}
                                </Col>
                            </Row>
                            <ToggleButtonGroup type="radio" className="module-toggle-group" name="moduleSelected" defaultValue={2} onChange={this.toggleModule}>
                                <ToggleButton variant="Light" className="module-toggle-button" value={1}>Digits</ToggleButton>
                                <ToggleButton variant="Light" className="module-toggle-button" value={2}>Letters</ToggleButton>
                            </ToggleButtonGroup>
                        </Container>
                    </div>
                </div>                      
            </>
        )
    }
}
const mapDispatchToProps = {
}

export default connect(null, mapDispatchToProps)(Freecam);
