import React , {SyntheticEvent} from 'react'
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import {resetPassword} from '../../../../state/actions/login';
interface SignInOptionsProps {
    id?: string;
    onSignUpClicked: (e: React.MouseEvent) => void;
    onLoginClicked: (event: React.MouseEvent) => void;
}
interface SignInState {
    passWordRecoverClicked: boolean;
    resetSent:boolean;
    resetWasValid:boolean;
    email:string;

}
class SignInOptions extends React.Component<SignInOptionsProps, SignInState>{
    constructor(props: SignInOptionsProps) {
        super(props);
        this.state = {
            passWordRecoverClicked: false,
            resetWasValid:false,
            resetSent: false,
            email:"",
        }
        this.openPasswordRecovery = this.openPasswordRecovery.bind(this);
        this.onChangeEmail = this.onChangeEmail.bind(this);
        this.handleResetClicked = this.handleResetClicked.bind(this);
    }
    openPasswordRecovery(){
        this.setState({passWordRecoverClicked:true});
    }
    onChangeEmail(event: SyntheticEvent){
        const email = (event.target as HTMLInputElement).value;
        this.setState({email});
    }
    handleResetClicked = async (e: React.MouseEvent) => {
        const response:boolean = await resetPassword(this.state.email);
        this.setState({resetSent:true});
        this.setState({resetWasValid:response})
    }
    render() {
        let resetMessage:any = "";
        if(this.state.resetSent){
            if(this.state.resetWasValid){
                resetMessage = <p>A new Password has been sent to your email!</p>
            }
            else{
                resetMessage = <p>Invalid Email please try again</p>
            }
        }
        let passwordRecoveryForm  = <button onClick={this.openPasswordRecovery}>click here</button>;
        if (this.state.passWordRecoverClicked){
            passwordRecoveryForm = <Form>
            <Form.Row>
                <Form.Group as={Col} controlId="ResetPassword">
                    <Form.Control
                        type="text"
                        data-cy='username-input'
                        placeholder="Email"
                        onChange={this.onChangeEmail}
                    />
                </Form.Group>
                {resetMessage}
                <Button onClick={this.handleResetClicked}>
                    Reset Password
                </Button>
             </Form.Row>
        </Form>
        }
        return (
            <div id={this.props.id}>
                <Button data-cy='submit-login' id="modalSignInButton" variant="primary" type="submit" onClick={this.props.onLoginClicked}>
                    Login
                    </Button>
                <p className="horizontalLine"><span>or</span></p>
                <p>Forgot your password?</p>  
                {passwordRecoveryForm}

                <p className="horizontalLine"><span>or</span></p>

                <p>Don't have an account?</p>

                <Button
                    id="modalLoginButton"
                    variant="primary"
                    type="submit"
                    onClick={this.props.onSignUpClicked}
                >
                    Sign Up
                </Button>
            </div>
        )
    }
}
export default SignInOptions;
