import {EDIT, SESSION} from '../models/types';
import axios from "axios";
import store from "../store";
import {BASE_URL} from '../../environment'
import {updateSession} from "./session";
import {RequestName, STACK} from "../models/analyticsModels";
import {logError, logServerRequestTime} from "./analytics";

export async function editProfile(data: any) {
    const errors: any = {};

    try{
        const sentTime = (new Date()).getTime();
        const response = await axios.post(BASE_URL + '/user/edit', {
            "username": data.username,
            "new_username": data.new_username,
            "email": data.email,
            "password": data.password,
            "new_password": data.new_password

        })
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.EDIT_PROFILE, receivedTime-sentTime);

        updateSession(SESSION, response.data)

        store.dispatch({
            type: EDIT,
            payload: response.data.username
        });

        return {
            errors,
            isSuccessful: true
        }
    }
    catch(e){
        if(e.response?.data?.Error) {
            const res = e.response.data.Error;
            if(res.includes('Password')){
                errors.password = "Error with the password";
            }

            if(res.includes('Username')) {
                errors.newUsername = "Username is already taken";
            }

            if(res.includes('Email')) {
                errors.email = "Email is already taken";
            }
            logError(RequestName.EDIT_PROFILE, res, STACK.SERVER)

        } else {
            errors.password = "Unsuccessful request";
            logError(RequestName.EDIT_PROFILE, "Unsuccessful request", STACK.SERVER)
        }


        return {
            errors,
            isSuccessful: false
        };
    }
}

export async function getPreferences() {
    const errors: any = {};

    try{
        const sentTime = (new Date()).getTime();
        const response = await axios.get(BASE_URL + '/user/settings/list')
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.GET_PREFERENCES, receivedTime-sentTime);

        return {
            errors,
            isSuccessful: true,
            data: response.data.response_text
        }

    } catch(e) {
        logError(RequestName.GET_PREFERENCES, "Unsuccessful request", STACK.SERVER)
        return {
            errors: e,
            isSuccessful: false
        };
    }
}

export async function getUserPreferences() {
    const errors: any = {};

    try{
        const username: any = await JSON.parse(localStorage.getItem(SESSION) || '{}').user.username
        const sentTime = (new Date()).getTime();
        const response = await axios.get(BASE_URL + '/user/settings/user' + `?username=${username}`)
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.GET_USER_PREFERENCES, receivedTime-sentTime);

        return {
            errors,
            isSuccessful: true,
            data: response.data.response_text
        }

    } catch(e) {
        logError(RequestName.GET_USER_PREFERENCES, "Unsuccessful request", STACK.SERVER)
        return {
            errors: e,
            isSuccessful: false
        };
    }
}

export async function setUserPreferences(id: string, val: boolean) {
    const errors: any = {};

    try{
        const username: any = await JSON.parse(localStorage.getItem(SESSION) || '{}').user.username;

        const sentTime = (new Date()).getTime();
        await axios.post(BASE_URL + '/user/settings/update' + `?username=${username}`,
            {
                'username': username,
                'setting_id': id,
                'isTrue': val
            })
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.SET_USER_PREFERENCES, receivedTime-sentTime);

        return {
            errors,
            isSuccessful: true
        }

    } catch(e) {
        logError(RequestName.SET_USER_PREFERENCES, "Unsuccessful request", STACK.SERVER)
        return {
            errors: e,
            isSuccessful: false
        };
    }
}

