import React from "react";
import { connect } from "react-redux";
import "./IndividualQuiz.css";
import MultipleChoice from "../multiple-choice/MultipleChoice";
import MatchAndSign from "../match-and-sign/MatchAndSign";
import FillTheBlanks from "../fill-the-blanks/FillTheBlanks";
import { getQuizData, getQuizzes } from "../../../../../state/actions/quizzes";
import store from "../../../../../state/store";
import Header from "../../../../../common/components/header/Header";
import { Row, Col, Button} from "react-bootstrap";
import { MdClose } from "react-icons/md";
import { QuizDetails, QuizQuestion } from "../types/Quiz";
import { Paths } from "../../../../../App";
import {Link, NavLink} from "react-router-dom";

export interface IndividualQuizProps {
    location: object;
    match: {
        params: {
            quizId: string;
            moduleId: string;
        }
    };
}
export interface IndividualQuizState {
    username: string;
    nbRightAnswers: number;
    questions: QuizQuestion[];
    questionIndex: number;
    quizDetails: QuizDetails;
    isAnswered: boolean;
}
class IndividualQuiz extends React.Component<IndividualQuizProps, IndividualQuizState>{
    constructor(props: IndividualQuizProps) {
        super(props);
        this.state = {
            nbRightAnswers: 0,
            questions: [],
            username: store.getState().user.username,
            questionIndex: 0,
            quizDetails: {},
            isAnswered: false
        }
    }
    setAsAnswered = (e: any, isCorrect?: boolean) => {
        if(isCorrect)
        {
            this.setState({nbRightAnswers: this.state.nbRightAnswers + 1})
        }
        this.setState({isAnswered: true});
    }
    getCurrentQuestion = () => {
        return this.state.questions[this.state.questionIndex];
    }
    incrementQuestionIndex = () => {
        this.setState({ questionIndex: this.state.questionIndex + 1, isAnswered: false});
    }
    decrementQuestionIndex = () => {
        this.setState({ questionIndex: this.state.questionIndex - 1 });
    }

    skip = () => {
        this.setState({isAnswered: true});

        if (this.state.questionIndex < this.state.questions.length - 1) {
            this.incrementQuestionIndex();
        } else {
            // TODO get quiz results
        }
    }

    async componentDidMount() {
        const questions: QuizQuestion[] = await getQuizData(this.props.match.params.quizId);
        const quizzesDetails: QuizDetails[] = await getQuizzes(this.props.match.params.moduleId);
        const quizDetails: QuizDetails = quizzesDetails
            .filter(quizDetail => quizDetail.quiz_id === parseInt(this.props.match.params.quizId, 10))[0];
        this.setState({ questions: questions, quizDetails: quizDetails });
    }
    getQuizComponentByType() {
        if (this.getCurrentQuestion().q_type === 'multiple-choice') {
            return <MultipleChoice key={this.getCurrentQuestion().q_id} question={this.getCurrentQuestion()} onQuestionAnswered={this.setAsAnswered} />
        } else if (this.getCurrentQuestion().q_type === 'fill-the-blanks') {
            return <FillTheBlanks key={this.getCurrentQuestion().q_id} question={this.getCurrentQuestion()} onQuestionAnswered={this.setAsAnswered} />
        } else if (this.getCurrentQuestion().q_type === 'match-and-sign') {
            return <MatchAndSign key={this.getCurrentQuestion().q_id}
                                 module={this.props.match.params.moduleId}
                                 question={this.getCurrentQuestion()}
                                 onQuestionAnswered={this.setAsAnswered}
                                 onQuestionSkipped={this.skip}  />
        }
    }
    calculateMark() {
        return (this.state.nbRightAnswers/this.state.questions.length)*100
    }
    render() {
        return (
            <>
                <Header />
                <Row className="quizHeader">
                    <Col xs={4} md={4} lg={4}>
                        <h3>{this.state.quizDetails.title}</h3>
                    </Col>
                    
                    <Col lg={{ span: 2, offset: 6 }}>
                        <NavLink to={Paths.quizzesModules}>
                            <MdClose />
                        </NavLink>

                    </Col>
                </Row>
                <div className="individual-quiz-wrapper content-top-spacing">
                    {(this.getCurrentQuestion())
                        ? this.getQuizComponentByType()
                        : null
                    }
                </div>
                <Row>
                    {/* <Col xs={4} md={4} lg={{ span: 1, offset: 2 }}>
                        {this.state.questionIndex === 0 ? null : <Button onClick={this.decrementQuestionIndex}>Previous</Button>}
                    </Col> */}
                    <Col lg={{ span: 1, offset: 9 }}>
                        {this.state.questionIndex >= this.state.questions.length - 1
                            ?
                                <Link to={Paths.score.replace(':mark', this.calculateMark().toString())}>
                                    <Button disabled={!this.state.isAnswered}>Finish</Button>
                                </Link>
                            :
                            <Button disabled={!this.state.isAnswered} onClick={this.incrementQuestionIndex}>Next</Button>}
                    </Col>
                </Row>
            </>
        )
    }
}
const mapDispatchToProps = {
}
export default connect(null, mapDispatchToProps)(IndividualQuiz);
